import { useEffect, useState } from 'react';
import useLocales from '../../hooks/useLocales';

import { fNumber } from '../../utils/formatNumber';

import { BlurModalBox } from '../../components/StyledComponents';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';

export default function TicketReturnModal({ ticket, onClose, onSend }) {
    const { t } = useLocales();
    const [content, setContent] = useState('');
    const [file, setFile] = useState({});
    const [existsFile, setExistsFile] = useState(false);

    const onSubmit = () => {
        onSend({
            ticketId: ticket?._id,
            file,
            content
        });
    };

    const onUploadFile = (e) => {
        if (e.target?.files[0])
            setExistsFile(true);
        else
            setExistsFile(false);
        setFile(e.target?.files[0]);
    }

    useEffect(() => {
        setContent(ticket?.return?.content);
    }, [ticket])

    return (
        <>
            <input type="checkbox" id="ticket-return-modal" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="ticket-return-modal"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            ✕
                        </label>
                        <h3 className="font-bold text-lg capitalize">{t('action.return_ticket')}</h3>
                    </div>
                    <hr className="-mx-5 border-stone-500" />
                    <div className="flex flex-col gap-4 p-2 mb-4">
                        <span className="text-stone-400 -mb-2">{t("title.ticket")} : </span>
                        <input type="file" accept=".jpg, .png, .bmp, .webp, .pdf" onChange={onUploadFile} className="file-input file-input-bordered file-input-warning w-full mt-2" />
                        <span className="text-stone-400 -mb-2">{t("label.message")} : </span>
                        <GradientBorderWrapper>
                            <div className="p-2 w-full flex">
                                <textarea
                                    className="textarea w-full"
                                    rows={2}
                                    value={content}
                                    onChange={(e) => setContent(e.target.value)}
                                />
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <div className="flex justify-between gap-2 p-2">
                        <button
                            className="btn btn-primary px-8 capitalize btn-outline"
                            onClick={() => onClose('cancel')}
                        >
                            {t('action.cancel')}
                        </button>
                        <button className="btn btn-primary px-8 capitalize" disabled={!content || !existsFile} onClick={onSubmit}>
                            {t(`action.${ticket?.return?.file ? "update" : "continue"}`)}
                        </button>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}
