import { useEffect, useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import useLocales from '../../hooks/useLocales';
import { HOST_API, WEBSOCKET_CONSTATNT, API_CHAT, API_TICKET } from '../../config';
import axios from '../../utils/axios';
import { Icon } from '@iconify/react';
import { fDescriptionTypeDate, fToNow } from '../../utils/formatTime';
import GradientBorderWrapper from '../wrappers/GradientBorderWrapper';
import useWebsocket from '../../hooks/useWebsocket';
import Chip from '../../components/Chip';

export default function EventTicketList({ ticket, event, type, onDispute, onReturn, onRefund }) {
    const { t } = useLocales();
    const navigate = useNavigate();
    const { received } = useWebsocket();
    const [showCancel, setShowCancel] = useState(true);
    const [disabled, setDisabled] = useState(false);
    const [disputed, setDisputed] = useState(false);

    let ChipColor = "success";
    if (ticket.status === "inprogress")
        ChipColor = "red-500"
    else if (ticket.status !== "sold")
        ChipColor = "sky";

    const getDisputeTicket = async () => {
        await axios.get(`${API_CHAT.getDisputeTicket}/${ticket._id}`).then(({ status, data }) => {
            if (status === 200) {
                if (data.data?.dispute)
                    setDisputed(true);
                else
                    setDisabled(!ticket?.dispute);
            }
        });
    }

    const onCancel = async () => {
        await axios.post(API_TICKET.setCancelTicket, { ticketId: ticket._id }).then(({ status, data }) => {
            if (status === 200) {
                navigate("/ticket/my-tickets");
            }
        });
    }

    useEffect(() => {
        if (type === "my-ticket")
            getDisputeTicket();
    }, [ticket]);

    useEffect(() => {
        if (received && received?.command) {
            const { command, id } = received;
            if (command === WEBSOCKET_CONSTATNT.DISABLE_DISPUTE)
                if (id === ticket._id)
                    setDisabled(true);
            if (command === WEBSOCKET_CONSTATNT.DISABLE_CANCEL)
                if (id === ticket._id)
                    setShowCancel(false);
        }
    }, [received]);

    return (
        <div className="w-full h-full cursor-pointer">
            <GradientBorderWrapper>
                <div className="flex flex-col w-full gap-8 p-4 items-start">
                    <div className="flex gap-6 w-full">
                        <div className="avatar w-1/5 h-20">
                            <LazyLoadImage
                                src={`${HOST_API}${ticket.avatar}`}
                                alt=""
                                effect="blur"
                                className="aspect-square rounded-xl"
                            />
                        </div>
                        <div className="flex flex-col w-full">
                            <label className="text-[19px] capitalize">{event?.name}</label>
                            <span className="text-base text-stone-400">
                                {fDescriptionTypeDate(ticket?.dateTime)}
                            </span>
                            <div className="flex justify-between" >
                                <span className="text-sm text-primary">{`1 ${t('title.ticket')}`}</span>
                                <span className="text-sm text-stone-400">{`${fToNow(ticket.purchasedTime)}`}</span>
                            </div>
                        </div>
                    </div>

                    <GradientBorderWrapper
                        className="w-full p-4 event-active-bg"
                        style={{ justifyContent: 'start' }}
                    >
                        <div className='flex gap-4'>
                            <div>
                                <svg
                                    width="24"
                                    height="24"
                                    viewBox="0 0 24 24"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                >
                                    <path
                                        d="M12 24C15.1826 24 18.2348 22.7357 20.4853 20.4853C22.7357 18.2348 24 15.1826 24 12C24 8.8174 22.7357 5.76516 20.4853 3.51472C18.2348 1.26428 15.1826 0 12 0C8.8174 0 5.76516 1.26428 3.51472 3.51472C1.26428 5.76516 0 8.8174 0 12C0 15.1826 1.26428 18.2348 3.51472 20.4853C5.76516 22.7357 8.8174 24 12 24ZM13.395 9.882L11.895 16.9395C11.79 17.4495 11.9385 17.739 12.351 17.739C12.642 17.739 13.0815 17.634 13.38 17.37L13.248 17.994C12.8175 18.513 11.868 18.891 11.0505 18.891C9.996 18.891 9.5475 18.258 9.8385 16.9125L10.9455 11.7105C11.0415 11.271 10.9545 11.112 10.515 11.0055L9.8385 10.884L9.9615 10.3125L13.3965 9.882H13.395ZM12 8.25C11.6022 8.25 11.2206 8.09196 10.9393 7.81066C10.658 7.52936 10.5 7.14782 10.5 6.75C10.5 6.35218 10.658 5.97064 10.9393 5.68934C11.2206 5.40804 11.6022 5.25 12 5.25C12.3978 5.25 12.7794 5.40804 13.0607 5.68934C13.342 5.97064 13.5 6.35218 13.5 6.75C13.5 7.14782 13.342 7.52936 13.0607 7.81066C12.7794 8.09196 12.3978 8.25 12 8.25Z"
                                        fill="#F2B705"
                                    />
                                </svg>
                            </div>
                            <div className='flex flex-col gap-2'>
                                <p className='text-[19px]'>{t('description.sold_ticket_desc')}</p>
                                <div>
                                    <p className='text-[19px]'>{`1 ${t('title.ticket')}`}</p>
                                    <p className='text-sm text-stone-400'>{event?.name}</p>
                                </div>
                                <div className='flex gap-6 items-center flex-wrap'>
                                    <Link to={`/ticket/manage/${ticket._id}`} className="text-primary underline">
                                        {t('action.see_ad')}
                                    </Link>
                                    <span className="text-primary underline">
                                        {t('action.help')}
                                    </span>

                                    <a href={`${HOST_API}${ticket?.return?.file ? ticket?.return?.file : ticket?.uploadedFile}`} download className="text-primary">
                                        <Icon icon="et:download" width={25} height={20} />
                                    </a>

                                    {event?.removed && ticket?.return?.state && type === "my-ticket" && (
                                        <button className={`btn btn-primary${ticket?.return?.file ? "/100" : ""} btn-sm`} onClick={onReturn} >
                                            {t(`action.${ticket?.return?.file ? "returned" : "return_ticket"}`)}
                                        </button>
                                    )}
                                    {event?.removed && ticket?.return?.state && type !== "my-ticket" && (
                                        <button className="btn btn-primary btn-sm tooltip" disabled={!ticket?.return?.file || ticket?.refund} onClick={onRefund} data-tip={t(`action.refund_payment`)} >
                                            {t(`action.${ticket?.refund ? "refunded" : "refund"}`)}
                                        </button>
                                    )}
                                    {(!event?.removed && type === "my-ticket") && (
                                        <>
                                            <button className="btn btn-primary btn-sm" onClick={onDispute} disabled={disputed || disabled}>
                                                {t(`action.${disputed ? "disputed" : "dispute"}`)}
                                            </button>
                                            {showCancel && (
                                                <button className="btn btn-primary btn-sm" onClick={onCancel}>
                                                    {t("action.cancel")}
                                                </button>
                                            )}
                                        </>
                                    )}
                                    {!ticket?.return?.state && type !== "my-ticket" && (
                                        <Chip label={ticket?.status?.replaceAll("_", " ")} color={ChipColor} />
                                    )}
                                </div>
                            </div>
                        </div>
                    </GradientBorderWrapper>
                </div>
            </GradientBorderWrapper>
        </div>
    );
}
