import { useEffect, useState } from 'react';
import { useLocation, useParams, useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

import { API_EVENT, HOST_API, API_CHAT, API_TICKET } from '../../config';
import useLocales from '../../hooks/useLocales';
import axios from '../../utils/axios';
import { fDescriptionTypeDate } from '../../utils/formatTime';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import EventTicketList from '../../components/cards/EventTicketList';
import GradientBorderWrapper from '../../components/wrappers/GradientBorderWrapper';
import TicketDisputeModal from '../../sections/ticket/TicketDisputeModal';
import TicketReturnModal from '../../sections/ticket/TicketReturnModal';
import Chip from '../../components/Chip';

export default function Event() {
    const location = useLocation();
    const navigate = useNavigate();
    const { t } = useLocales();
    const { eventId } = useParams();
    const [tickets, setTickets] = useState([]);
    const [currentEvent, setCurrentEvent] = useState();
    const [type, setType] = useState("");
    const [disputeTicket, setDisputeTicket] = useState({});
    const [returnTicket, setReturnTicket] = useState({});
    const [processing, setProcessing] = useState(false);

    const onDispute = (ticket) => {
        if (type === "my-ticket") {
            setDisputeTicket(ticket);
            document.querySelector('#ticket-dispute-modal').click();
        }
    }

    const onReturn = (ticket) => {
        if (type === "my-ticket") {
            setReturnTicket(ticket);
            document.querySelector('#ticket-return-modal').click();
        }
    }

    const onRefund = (ticket) => {
        if (type !== "my-ticket") {
            setProcessing(true);
            axios
                .post(API_TICKET.refundTicket, { ticketId: ticket._id })
                .then(({ status, data }) => {
                    if (status === 200)
                        toast.success(data?.message);
                })
                .catch((err) => { })
                .finally(() => {
                    setProcessing(false);
                });
        }
    }

    const onSendDisputeRequest = ({ ticketId, content }) => {
        setProcessing(true);
        axios
            .post(API_CHAT.sendDisputeRequest, {
                ticketId,
                content
            })
            .then(({ status }) => {
                if (status === 200)
                    navigate("/conversation/list");
            })
            .catch((err) => { })
            .finally(() => {
                setProcessing(false);
            });
    }

    const onSendReturnTicket = ({ ticketId, content, file }) => {
        setProcessing(true);
        const form = new FormData();
        form.append('ticketId', ticketId);
        form.append('content', content);
        form.append('file', file);
        axios
            .post(API_TICKET.returnTicket, form)
            .then(({ status, data }) => {
                if (status === 200)
                    toast.success(data?.message);
                document.querySelector('#ticket-return-modal').click();
            })
            .catch((err) => { })
            .finally(() => {
                setProcessing(false);
            });
    }

    const getEventData = async (_type = type) => {
        await axios
            .get(`${API_EVENT.getMyEventDetail}/${eventId}${type !== undefined ? `?type=${_type}` : ''}`)
            .then((res) => {
                setCurrentEvent(res.data.data);
                setTickets(res.data.data?.tickets || []);
            });
    }
    useEffect(() => {
        const _type = location.state?.type;
        setType(_type);
        if (eventId) {
            getEventData(_type)
        }
    }, [eventId, location]);

    return (
        <Page title="Event Detail">
            <PageBanner>
                <div className="container p-4">
                    <div className="w-full justify-center flex flex-col mb-16">
                        <h4 className="text-4xl md:text-[64px]">{t(`title.${type === "my-list" ? "menu.my_listing" : "tickets"}`)}</h4>
                        <p className="text-stone-400 mt-4">
                            {
                                type === "my-list" ?
                                    t(`description.${tickets.length ? 'my_listing' : 'empty_my_listing'}`) :
                                    t(`description.my_tickets`)
                            }
                        </p>
                    </div>
                    <div className="w-full md:w-1/2 mb-8" >
                        <GradientBorderWrapper className="flex px-6 py-[19px]">
                            <div className="flex flex-col gap-4">
                                <div className="flex gap-6">
                                    <img
                                        src={`${HOST_API}uploads/ticket_avatar/default-ticket.svg`}
                                        className="w-16 h-20 rounded-xl "
                                        alt=""
                                    />
                                    <div className="flex items-stretch justify-between flex-col gap-1 w-full">
                                        <label className="text-[19px] capitalize">{currentEvent?.name}</label>
                                        <span className="text-base text-stone-400">
                                            {fDescriptionTypeDate(currentEvent?.dateTime)}
                                        </span>
                                        <div className="flex justify-between">
                                            <span className="text-sm text-primary">{`${currentEvent?.tickets?.length} ${t('title.tickets')}`}</span>
                                            {currentEvent?.removed && <Chip label={t('label.removed')} color="red-500" />}
                                        </div>
                                    </div>
                                </div>
                                <span className="text-primary underline">
                                    {t('action.see_ad')}
                                </span>
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <h4 className="text-[28px] w-1/2 mb-4">{t('action.past_events')}</h4>
                    <div className="grid md:grid-cols-2 gap-8 mb-8">
                        {currentEvent &&
                            tickets?.map((t, index) => (
                                <EventTicketList
                                    ticket={t} key={index}
                                    event={currentEvent}
                                    type={type}
                                    onDispute={() => onDispute(t)}
                                    onReturn={() => onReturn(t)}
                                    onRefund={() => onRefund(t)}
                                />
                            ))}
                    </div>
                </div>
                {!currentEvent?.removed && type === "my-ticket" && (
                    <TicketDisputeModal
                        onSend={onSendDisputeRequest}
                        ticket={disputeTicket}
                        onClose={() => {
                            document.querySelector('#ticket-dispute-modal').click();
                        }}
                    />
                )}
                {currentEvent?.removed && type === "my-ticket" && (
                    <TicketReturnModal
                        onSend={onSendReturnTicket}
                        ticket={returnTicket}
                        onClose={() => {
                            document.querySelector('#ticket-return-modal').click();
                        }}
                    />
                )}
            </PageBanner>
        </Page>
    );
}
